.location-label {
  position: fixed;
  left: 50px;
  bottom: 50px;
  .hide-btn {
    display: none;
  }
  button {
    padding: 1em 2em;
    border: none;
    background-color: #8691b9;
    border-radius: 10px;
    color: white;
    outline: none;
    transition: 0.2s;
    cursor: pointer;

    &:hover {
      background-color: #98c3b4;
    }
  }
}
